import React from "react";
import "./style.css";
import ReactHtmlParser, {
  processNodes,
  convertNodeToElement,
  htmlparser2
} from "react-html-parser";

class Main extends React.Component {
  state = { name: "", sex: "", age: "", weight: "" };

  onClick = e => {
    e.preventDefault();

    this.setState({
      name: document.getElementById("name").value,
      sex: document.getElementById("sex").value,
      age: parseInt(document.getElementById("age").value),
      weight: parseInt(document.getElementById("weight").value),
      height: parseInt(document.getElementById("height").value)
    });
  };

  ppm = () => {
    if (this.state.sex === "Mężczyzna") {
      let ppm =
        13.7 * this.state.weight +
        5 * this.state.height -
        6.76 * this.state.age +
        66;

      let one_kg = 8000 / 30;
      let i = 0;
      let kg = 0.5;
      let html = "";
      while (i < 1) {
        if (ppm * 1.2 - kg * one_kg >= ppm) {
          html += `<div class="name">
            <b>
            <div>Tempo chudnięcia - ${kg}kg w miesiąc:</div>
            <div>${Math.round(ppm * 1.2 - kg * one_kg)} kcal</div>
            </b>
          </div>`;
          kg += 0.5;
        } else {
          i = 1;
        }
      }

      return (
        <div className="calories">
          <div className="name ppm">
            <b>
              <div>PODSTAWOWA PRZEMIANA MATERII:</div>
              <div>{Math.round(ppm)} kcal</div>
            </b>
          </div>

          <div className="name ppm">
            <b>
              <div>BMR - UTRZYMYWANIE WAGI:</div>
              <div>{Math.round(ppm * 1.2)} kcal</div>
            </b>
          </div>

          {ReactHtmlParser(html)}
        </div>
      );
    } else {
      let ppm =
        655 +
        9.6 * this.state.weight +
        1.8 * this.state.height -
        4.7 * this.state.age;

      let one_kg = 8000 / 30;
      let i = 0;
      let kg = 0.5;
      let html = "";
      while (i < 1) {
        if (ppm * 1.2 - kg * one_kg >= ppm) {
          html += `<div class="name">
            <b>
            <div>Tempo chudnięcia - ${kg}kg w miesiąc:</div>
            <div>${Math.round(ppm * 1.2 - kg * one_kg)} kcal</div>
            </b>
          </div>`;
          kg += 0.5;
        } else {
          i = 1;
        }
      }

      return (
        <div className="calories">
          <div className="name ppm">
            <b>
              <div>PODSTAWOWA PRZEMIANA MATERII:</div>
              <div>{Math.round(ppm)} kcal</div>
            </b>
          </div>

          <div className="name ppm">
            <b>
              <div>BMR - UTRZYMYWANIE WAGI:</div>
              <div>{Math.round(ppm * 1.2)} kcal</div>
            </b>
          </div>

          {ReactHtmlParser(html)}
        </div>
      );
    }
  };

  render() {
    if (this.state.name) {
      return (
        <div className="main">
          <div className="name">
            <b>
              {this.state.name}, {this.state.age}
            </b>
          </div>
          <div className="name sex">
            <b>{this.state.sex}</b>
          </div>
          <div className="name sex">
            <b>{this.state.weight}</b>
          </div>

          {this.ppm()}
        </div>
      );
    } else {
      return (
        <div className="main">
          <div className="navbar">My calories</div>

          <form>
            <label>Imię</label>
            <input type="text" id="name" name="name" />

            <label>Płeć</label>
            <select id="sex">
              <option value=""></option>
              <option value="Mężczyzna">mężczyzna</option>
              <option value="Kobieta">kobieta</option>
            </select>

            <label>Wiek</label>
            <input type="text" id="age" name="age" />

            <label>Waga</label>
            <input type="text" id="weight" name="weight" />

            <label>Wzrost</label>
            <input type="text" id="height" name="height" />

            <label>.</label>
            <button id="button" onClick={this.onClick}>
              Zatwierdź
            </button>
          </form>

          <footer className="navbar">Mikołaj Sojka</footer>
        </div>
      );
    }
  }
}

export default Main;
